import React, { Component, cloneElement } from 'react';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import { isFunction } from '@elliemae/ds-shared/utils';
import { InputAddonWrapper } from '@elliemae/ds-basic/form/Input';

const blockName = 'expandable-input';

const ExpandableInput = aggregatedClasses('div')(
  blockName,
  null,
  ({ isOpen }) => ({ opened: isOpen }),
);
const InputWrapper = aggregatedClasses(InputAddonWrapper)(
  blockName,
  'wrapper',
  ({ isOpen }) => ({ opened: isOpen }),
);

export default class ExpandableInputImpl extends Component {
  componentDidUpdate(prevProps) {
    const { isOpen } = this.props;
    if (isOpen && isOpen !== prevProps.isOpen) this.input.focus();
  }

  render() {
    const {
      innerRef,
      triggerComponent,
      children,
      width,
      isOpen,
      onOpen,
      onClose,
      onBlur,
      containerProps,
    } = this.props;
    const InputComponent = React.Children.only(children);
    const handleTriggerClick = e => {
      if (isFunction(triggerComponent.onClick)) triggerComponent.onClick(e);
      if (isOpen) {
        onClose();
      } else {
        onOpen();
      }
    };

    return (
      <ExpandableInput
        {...containerProps}
        classProps={{ isOpen }}
        innerRef={innerRef}
        style={{ width: isOpen && width }}
      >
        <InputWrapper
          rightComponents={[
            cloneElement(triggerComponent, {
              key: 'handler',
              onClick: handleTriggerClick,
            }),
          ]}
        >
          {cloneElement(InputComponent, {
            className: 'expandable-input__input',
            innerRef: node => (this.input = node),
            onBlur,
          })}
        </InputWrapper>
      </ExpandableInput>
    );
  }
}
