import React, { Component } from 'react';
import { debounce } from '@elliemae/ds-utilities/utils';
import SearchIcon from '@elliemae/ds-icons/Search';
import DSTextBox from '@elliemae/ds-basic/form/TextBox';
import DSButton from '@elliemae/ds-basic/Button';
import DSExpandableInput from '@elliemae/ds-basic/form/ExpandableInput';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import withSearchable from './withSearchable';

const blockName = 'searchbox-toggle';
const SearchBoxContainer = aggregatedClasses('div')(blockName);

class SearchBoxToggle extends Component {
  hideInput = debounce(this.handleHideInput, 100);

  showInput = debounce(this.handleShowInput, 100);

  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
    };

    this.hideInput = this.hideInput.bind(this);
    this.showInput = this.showInput.bind(this);
    this.handleHideInput = this.handleHideInput.bind(this);
    this.handleShowInput = this.handleShowInput.bind(this);
  }

  componentDidUpdate(prevProps) {
    const { clearOnSearch, value } = this.props;
    const { value: prevValue } = prevProps;
    if (
      clearOnSearch &&
      value !== prevValue &&
      !value &&
      prevValue &&
      prevValue.length !== 1
    ) {
      this.handleHideInput(false);
    }
  }

  handleSearch() {
    const { onSearch, clearOnSearch } = this.props;
    onSearch();
    if (clearOnSearch) this.setState({ isOpen: false });
  }

  handleHideInput(executeIfValue = true) {
    const { value } = this.props;
    if (value) {
      if (executeIfValue) this.handleSearch();
    } else {
      this.setState({ isOpen: false });
    }
  }

  handleBlur(e) {
    if (this.expandableEl.contains(e.target)) return;
    this.hideInput(false);
  }

  handleShowInput() {
    this.setState({ isOpen: true });
  }

  render() {
    const {
      onChange,
      onKeyUp,
      value,
      placeholder,
      clearable,
      className,
      buttonRef,
      containerProps,
      width = 300,
    } = this.props;
    const { isOpen } = this.state;
    return (
      <SearchBoxContainer>
        <DSExpandableInput
          containerProps={containerProps}
          innerRef={node => (this.expandableEl = node)}
          isOpen={isOpen}
          onBlur={this.handleBlur}
          onClose={this.hideInput}
          onOpen={this.showInput}
          triggerComponent={
            <DSButton
              key="handler"
              buttonType="text"
              icon={<SearchIcon />}
              innerRef={buttonRef}
            />
          }
          width={width}
        >
          <DSTextBox
            className={`expandable-input__input ${className}`}
            clearable={clearable}
            onChange={onChange}
            onKeyUp={onKeyUp}
            placeholder={placeholder}
            showIcon={false}
            value={value}
          />
        </DSExpandableInput>
      </SearchBoxContainer>
    );
  }
}

export default withSearchable(SearchBoxToggle);
